body{
  /* display: flex; */
  box-sizing: border-box;

}
.landing-contianer{
  margin: auto;
  margin-top: 20vh;
  height: 350px;
  width: 350px;
  border-radius: 10px;
  background: #252D4A;
  color: white;
  padding: 2rem;
  box-shadow: 10px 10px 42px 0 rgba(0,0,0,.75);
}
.start-btn{
  /* margin-top: 70px; */
  background: #8305BD;
  border: none;
  color: white;
  font-size: large;
  line-height: 2rem;
  padding: .3rem 1rem;
  border-radius: 10px;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}
.start-btn:hover{
  background-color: #7307a5;
}
.option-container{
  display: flex;
  flex-direction: column;
}
.option-btn{
  height: 2.7rem;
  margin: 2px;
  /* width: 100px; */
  border: 5px solid #234668;
  background: transparent;
  color: white;
  border-radius: 15px;
}
.option-btn:hover{
  background-color: #234668;
}
img{
  height: 200px;
  width: 200px;
}
